<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">资讯管理</el-row>
    <el-row type="flex" justify="end" style="margin-top: 10px;">
      <el-button plain size="small">
        <router-link :to="{ path: '/information/create'}"> 添加资讯</router-link>
      </el-button>
    </el-row>
    <!-- 资讯列表 -->
    <el-row style="margin-top: 30px;">
      <el-table :data="infomationList" stripe style="width: 100%">
        <el-table-column label="图片">
          <template slot-scope="scope">
            <img :src="scope.row.image && scope.row.image.url" style="width: 40px; height: 40px;background-color: #eee;">
          </template>
        </el-table-column>
        <el-table-column prop="title" label="标题" />
        <el-table-column prop="desc" label="描述" show-overflow-tooltip />
        <el-table-column prop="public_at" label="发布时间" show-overflow-tooltip />
        <el-table-column label="类别">
            <template slot-scope="scope">{{linkTypeMap[scope.row.link_type]}}</template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small">
              <router-link class='el-button--text' :to="{ path: `/information/${scope.row.id}/detail` }">编辑</router-link>
            </el-button>
            <el-button  @click="deleteHandleClick(scope.row)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row style="margin-top:30px;"  type="flex" justify="end">
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="curPage"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="10"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total" />
    </el-row>
  </div>
</template>
<script>

export default {
  data () {
    return {
      linkTypeMap: {
        'none': '不链接',
        'page': '自定义页面',
        'article': '链接公众号文章'
      },
      infomationList: [],
      curPage: 1,
      pageSize: 20,
      total: 0
    }
  },

  components: {
  },

  methods: {
    // 获取信息列表
    queryList () {
      let params = {
        page: this.curPage,
        per: this.pageSize
      }
      this.$http.informationList(params).then((res) => {
        if (!res) return
        this.infomationList = res.data.result_rows.informations
        this.total = res.data.result_rows.page.total_count
      })
    },
    // page大小改变
    handleSizeChange (page) {
      this.pageSize = page
      this.queryList()
    },

    // 当前page改变
    handleCurrentChange (page) {
      this.curPage = page
      this.queryList()
    },
    // 删除操作
    deleteHandleClick (row) {
      this.$confirm('确定删除吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
      .then((r) => {
        this.$http.deteleInfomation(row).then(res => {
          if (!res) return
          this.$message.success('操作成功')
          this.queryList()
        })
      })
      .catch(() => {})
    }
  },
  created () {
    this.queryList()
  }
}
</script>

